//import {useState, useRef } from 'react';
import React, { useState } from 'react';
import { useInView, animated, useSpring, config, easings, useScroll, useTrail, SpringValue  } from '@react-spring/web'
import { useNavigate } from 'react-router-dom';
import "./project.css"
import {projects} from './projects'
import { PieChart } from '@mui/x-charts/PieChart';
import { Box } from '@mui/material';

interface launguage2{
    lan: string,
    id: string,
}

type Language = 'cz' | 'en';  // Define a type for the valid keys

interface Translations {
    cz: {
        [key: string]: string;
    };
    eng: {
        [key: string]: string;
    };
}

interface Propss{
    project:{
        id:string,
        name:{
             text:{
                [key: string]:string,
             },
             color:string,
        },
        description:{
            [key: string]:string,
        },
        tags:Array<string>,
    },
    style: { [key: string]: SpringValue };
    nav:any,
    lan:string,
}

function getproject(id:string){
    let project = projects[1];
    projects.forEach(element => {
        if(element.id == id){
            project = element;
        }
    });
    return project;
}

interface p{
    isrc:string,
    style: { [key: string]: SpringValue };
}

const Img:React.FC<p> = ({ isrc, style}) => (
    <animated.div className='img' /*onclick={()=>resize(isrc)}*/ style={{backgroundImage:`url("../assets/projectimages/${isrc}")`,...style}}>
      
    </animated.div>
);

const Project: React.FC<launguage2> = ({lan, id}) => {
    const [ref, inView] = useInView();
    const [ref2, inView2] = useInView();
    const [ref3, inView3] = useInView();
    const [ref4, inView4] = useInView();
    //const targetRef = useRef(null);
    const [spr, ap] = useSpring(() => ({
        delay:1000,
        from: { x: '-100vw' },
        to: {x:'0vw'},
    }))
    const [spr2, ap2] = useSpring(() => ({
        delay:1200,
        from: { x: '-100vw' },
        to: {x:'0vw'},
    }))
    const [spr3, ap3] = useSpring(() => ({
        delay:1600,
        from: { x: '200vw' },
        to: {x:'0vw'},
    }))
    const navigate = useNavigate()
    function translate(msg: string, lng: string) {
        const translations: Translations = require('./translations').default;
        return translations[lng as keyof Translations][msg];
    }
    function redirect(type:string){
        if(type==="none"){
            window.location.replace("https://github.com/Jimmy-88");
        }
    }
    let projects = getproject(id)
    let graph = projects.graphdata ?? []
    let imgs = projects.imgs ?? []
    let trail = useTrail(imgs.length, {
        from: {x:"-200vh", y:"-200vh"},
        to: {x:"0vh", y:"0vh"},
        delay: 1600, // Delay before starting the animation
    });
    //alert('Lan change: '+lan);
    return (
    <div className="projectscreen">
        <animated.div className="name" style={{color:projects.name.color?projects.name.color:"black",...spr}}>
            {projects.name.text[lan as 'cz' | 'eng']} 
        </animated.div>
        <animated.div className="c" style={spr2}>
            <div className="desc">
                {projects.description[lan as 'cz' | 'eng']}
            </div>
            <div className="c2">
                <div className="images">
                    
                {trail?
                    trail.map((style, index) => (
                        <Img key={index} isrc={imgs? imgs[index] : "f"} style={style}/>
                    ))
                :
                <>
                </>
                }
                </div>
                <animated.div className="graph" style={spr3}>
                    <div className="glabel">
                        {translate("usedtechnologies",lan)}
                    </div>
                    <div className="g">
                        <PieChart
                            series={[
                                {
                                data:graph,
                                highlightScope: { faded: 'global', highlighted: 'item' },
                                faded: { innerRadius: 30, additionalRadius: -30, color: 'gray' },
                                },
                            ]}
                            slotProps={{
                                legend: { hidden: true },
                            }}
                            alignItems="center" 
                            justify="center"
                        />
                    </div>
                </animated.div>
            </div>
        </animated.div>
    </div>
      );
}
export {Project};