const Project:React.FC<Propss> = ({ project, style, nav, lan }) => (
    <animated.div className='project' style={style}>
      <div className="name" style={{color:project.name.color}}>
        {project.name.text[lan as string]}
      </div>
      <div className="description">
        {project.description[lan as string]}
      </div>
      <div className="tags">
        {project.tags.map((tag:string, index:number) => (
          <div key={index} className="tag">
            {tag}
          </div>
        ))}
      </div>
      <div className="buttons">
        <button className='b' onClick={()=>nav('/projects/'+project.id)}>{translate("details",lan)}</button>
      </div>
    </animated.div>
);

function translate(msg: string, lng: string) {
    const translations: Translations = require('./translations').default;
    return translations[lng as keyof Translations][msg];
}

const Projects: React.FC<launguage2> = ({lan}) => {
    const [ref, inView] = useInView();
    const [ref2, inView2] = useInView();
    const [ref3, inView3] = useInView();
    const [ref4, inView4] = useInView();
    //const targetRef = useRef(null);
    const [text,setText] = useState("Discord");
    const [spr, ap] = useSpring(() => ({
        delay:1000,
        from: { y: '-300vw' },
        to: {y:'0vw'},
    }))
    const navigate = useNavigate()
    function translate(msg: string, lng: string) {
        const translations: Translations = require('./translations').default;
        return translations[lng as keyof Translations][msg];
    }
    function redirect(type:string){
        if(type==="none"){
            window.location.replace("https://github.com/Jimmy-88");
        }
    }
    const trail = useTrail(projects.length, {
        from: {y:"-200vh"},
        to: {y:"0vh"},
        delay: 1000, // Delay before starting the animation
        config: { mass: 1, tension: 280, friction: 60 },
      });
    return (
    <div className="projectscreen">
        <div className='projectsbox'>
        {trail.map((style, index) => (
            <Project key={index} project={projects[index]} style={style} nav={navigate} lan={lan}/>
        ))}
        </div>
    </div>
      );
}
export {Projects, projects};